import { IDBPDatabase, openDB } from 'idb'

const initDB = async () => {
  const db = await openDB('kanji', 1, {
    upgrade: (db, _, version) => {
      console.log('Upgrading database.')
    }
  })

  return db
}

class KanjiDB {
  db: Promise<IDBPDatabase>

  constructor() {
    this.db = initDB()
  }
}

export default KanjiDB
